<template>
    <section class="pc-container pb-4">
        <div class="pcoded-content">
            <!-- <div class="page-header">
                <div class="page-block">
                    <div class="row align-items-center">
                        <div class="col-md-12">
                            <div class="page-header-title">
                                <h5 class="m-b-10">Alertas</h5>
                            </div>
                            <ul class="breadcrumb">
                                <li class="breadcrumb-item">
                                    <router-link to="/sisge">Inicio</router-link>
                                </li>
                                <li class="breadcrumb-item">Visualizacion</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div> -->
            <div class="row mb-4">
                <h5 class="text-dark fw-bold">Lista de Alertas</h5>
            </div>
            <div class="row">
                <div class="col-xl-12">
                    <div class="card shadow">
                        <div class="card-header">
                            <h5 class="text-secondary">Lista de Alertas</h5>
                        </div>
                        <div class="card-body">
                            <div class="table-responsive">
                                <table class="table" id="miTabla">
                                    <thead>
                                        <tr>
                                            <th class="col-md-4">N° de Expediente</th>
                                            <th class="col-md-3">Fecha de Vencimiento</th>
                                            <th class="col-md-5">Descripcion</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(audiencia, index) in audiencias" :key="index">
                                            <td class="col-md-4">
                                                <router-link
                                                    :to="{ name: 'expediente/detalle', params: { id: audiencia.ale_exp_id } }">{{
                                                        audiencia.ale_expediente }}</router-link>
                                            </td>
                                            <td class="col-md-3">{{ audiencia.fecha }}</td>
                                            <td class="col-md-5">{{ audiencia.ale_descripcion}}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
  
<script>
import $ from 'jquery';
import 'datatables.net-bs5';
import 'datatables.net-bs5/css/dataTables.bootstrap5.min.css';
import 'datatables.net-buttons-bs5';
import 'datatables.net-buttons-bs5/css/buttons.bootstrap5.min.css';
import feather from 'feather-icons';

import { mapActions } from 'vuex';

export default {
    name: 'audienciaAll',
    created() {
        this.listarAudiencias();
    },
    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    data() {
        return {
            audiencias: [],
            dataTable: null,
        };
    },
    methods: {
        ...mapActions(['get', 'post']),
        async listarAudiencias() {
            try {
                const response = await this.get({
                    url: this.$store.getters.get__url + '/alerta',
                    token: this.$store.getters.get__token,
                });
               
                    console.log(response);
                   
                this.audiencias = response.data;
                this.initDataTable();
            } catch (error) {
                console.error(error);
            }
        },
        initDataTable() {
            if (this.dataTable) {
                this.dataTable.destroy();
            }
            this.$nextTick(() => {
                this.dataTable = $('#miTabla').DataTable({
                    language: {
                        url: '//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json',
                    },
                });
            });
        },
        demandanteNombre(audiencia) {
            return audiencia.nat_apellido_paterno + ' ' + audiencia.nat_apellido_materno + ' ' + audiencia.nat_nombres;
        },
    },
    beforeDestroy() {
        if (this.dataTable) {
            this.dataTable.destroy();
        }
    },
};
</script>
  
<style scoped>
td {
    white-space: normal !important;
    word-wrap: break-word;
}
</style>
  